import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { ArrayMinSize, IsArray, IsEnum, IsNotEmpty, IsString, ValidateNested } from "class-validator";
import { Brand } from "../../../../orm-config/entity/payment/brand.entity";
import { ColumnExists } from "../../../../validation";
import { BrandType } from "../../../../orm-config/entity/enum/brand-type";
import { PaymentMode } from "../../../../orm-config/entity/enum/payment-mode";
import { CreatePaymentGatewayKeyInput } from "./create-payment-gateway-key-input";


@InputType()
export class CreateBrandInput {

  @Field(() => BrandType)
  @IsEnum(BrandType)
  brandType: BrandType;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  @ColumnExists(Brand)
  name: string;

  @Field(() => PaymentMode)
  @IsEnum(PaymentMode)
  paymentMode: PaymentMode;

  @Field(() => [CreatePaymentGatewayKeyInput])
  @ArrayMinSize(1)
  @ValidateNested({ each: true })
  @IsArray()
  @Type(() => CreatePaymentGatewayKeyInput)
  paymentGatewayKeys: CreatePaymentGatewayKeyInput[];

}
